




















































































































































html,body {
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
  -webkit-font-smoothing: antialiased;
  background-color: #303E4D;
}

::-moz-selection {
  background: #0F73EE;
  color: white;
}

::selection {
  background: #0F73EE;
  color: white;
}

.page {
  padding-top: 80px;
}

.banner {
  background-color: #222C36;
  text-align: center;
  left: 0;
  right: 0;
  padding: 20px;
  font-size: 12px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 0.2;
  color: #ACB1B7;
}

.intro {
  padding-bottom: 40px;
}

.footer {
  margin-top: 40px;
  padding-bottom: 40px;
}

.intro p.headline {
  color: #f1f1f1;
  font-size: 18px;
  width: 90%;
  margin-bottom: 10px;
  line-height: 24px;
}

.intro p,
.footer p {
  font-size: 15px;
  color: #ACB1B7;
  line-height: 21px;
}

.intro p a,
.footer p a {
  color: #ACB1B7;
  text-decoration: none;
  border-bottom: 1px solid #ACB1B7;
}

.intro p.headline a {
  color: #fff;
  border-color: #fff;
}

.page {
  max-width: 520px;
  margin: 0 auto;
}

.row {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
}

.droplet {
  height: 38px;
  width: 38px;
  margin-right: 10px;
  border-radius: 50%;
  border: 4px solid white;
  background-color: white;
  -webkit-transition: background 600ms cubic-bezier(0.165, 0.84, 0.44, 1);
  transition: background 600ms cubic-bezier(0.165, 0.84, 0.44, 1);
}

.hex {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  margin-right: 10px;
  z-index: 10;
}

.color-name {
  position: absolute;
  color: white;
  right: 75px;
  top: 15px;
  font-size: 15px;
  opacity: 0.8;
  z-index: 9;
}

.row input {
  font-family: 'Roboto Mono', monospace;
  font-size: 16px;
  border-radius: 50px;
  border: 0px solid;
  color: white;
  -webkit-transition: all 200ms cubic-bezier(0.165, 0.84, 0.44, 1);
  transition: all 200ms cubic-bezier(0.165, 0.84, 0.44, 1);
  background-color: rgba(0,0,0,0.3);
  padding-left: 20px;
  text-transform: uppercase;
}

.row input:focus {
  background-color: white;
  border: 0px;
  color: black;
  outline: none;
}

.expand-transition {
  -webkit-transition: all 200ms cubic-bezier(0.165, 0.84, 0.44, 1);
  transition: all 200ms cubic-bezier(0.165, 0.84, 0.44, 1);
  opacity: 1;
  height: 46px;
  padding-bottom: 20px;
}

.expand-enter, .expand-leave {
  opacity: 0;
  padding-bottom: 0;
  height: 0;
}

.pop-transition {
  -webkit-transition: all 200ms cubic-bezier(0.165, 0.84, 0.44, 1);
  transition: all 200ms cubic-bezier(0.165, 0.84, 0.44, 1);
  opacity: 1;
  -webkit-transform: scale(1);
          transform: scale(1);
}

.pop-enter, .pop-leave {
  opacity: 0;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}

.add {
  width: 46px;
  height: 46px;
  opacity: 1;
  border: 0;
  display: inline-block;
  border-radius: 50%;
  overflow: hidden;
  text-indent: -999px;
  background-color: transparent;
  background-image: url('/static/images/add.svg');
  background-position: center center;
  background-repeat: no-repeat;
  outline: none;
  cursor: pointer;
}

.download,
.woop {
  border: 4px solid #0F73EE;
  background-color: transparent;
  border-radius: 50px;
  height: 46px;
  float: right;
  outline: none;
  font-size: 12px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 1px;
  padding: 0px 30px;
  color: #0F73EE;
  line-height: 36px;
  cursor: pointer;
}

.woop {
  float: none;
  margin-top: 20px;
  width: 80%;
}

.remove {
  width: 46px;
  height: 46px;
  opacity: 0.4;
  border: 0;
  border-radius: 50%;
  overflow: hidden;
  text-indent: -999px;
  background-color: transparent;
  background-image: url('/static/images/remove.svg');
  background-position: center center;
  background-repeat: no-repeat;
  outline: none;
  cursor: pointer;
  -webkit-transition: all 300ms cubic-bezier(0.165, 0.84, 0.44, 1);
  transition: all 300ms cubic-bezier(0.165, 0.84, 0.44, 1);
}

.modal-bg {
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: #303E4D;
  z-index: 20;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.modal {
  background-color: #1F2A33;
  width: 380px;
  text-align: center;
  padding: 60px 20px;
  color: white;
  border-radius: 20px;
  box-shadow: 0px 5px 16px rgba(0,0,0,0.1);
}

.modal p.headline {
  width: 100%;
}




